  background: black;
}

body * {
  letter-spacing: 0.1rem;
  /* border: 0.2rem solid red; */
}

body {
  display: grid;
  grid-template: 1fr 8fr / 1fr 3.5fr 1.5fr;
  justify-items: center;
  gap: 3rem;
  padding: 0 2rem 2rem 2rem;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-position: center center;
  height: 100vh;
  position: relative;
  font-family: "Raleway", sans-serif;
}

body > div:first-child {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.first-sec {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  color: white;
  padding: 0.5rem;
  width: 100%;
  height: 100%;
  grid-row: 1 / -1;
  grid-column: 1 / span 1;
  z-index: 2;
}

.first-sec fieldset {
  padding: 1rem;
  border-radius: 1.5rem;
  border: 1px solid white;
  cursor: context-menu;
  transition: transform 0.3s ease-in-out;
}

.first-sec fieldset:hover,
fieldset.scale-up {
  transform: scale(1.1);
}

.first-sec legend {
  font-size: 0.8rem;
  border: 1px solid white;
  border-radius: 1rem;
  margin-left: 1rem;
  padding: 0.1rem 0.3rem;
}

.first-sec fieldset div {
  display: flex;
  gap: 0.5rem;
}

.first-sec div div {
  width: 2rem;
  height: 2rem;
}

.first-sec span {
  align-self: center;
}

.search-sec {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  grid-row: 1 / span 1;
  grid-column: 2 / span 1;
  z-index: 10;
}

.buttons-sec {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-row: 1 / span 1;
  grid-column: 3 / -1;
  z-index: 2;
}

.buttons-sec div {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 8rem;
  gap: 0.5rem;
}

button {
  color: white;
  background: linear-gradient(to bottom, rgb(0, 0, 0), rgb(2, 116, 116));
}

button:hover {
  color: white;
  background: rgb(0, 88, 88);
  cursor: pointer;
}

.buttons-sec button:first-child {
  width: 100%;
  font-weight: 500;
}

.buttons-sec button {
  font-size: 1rem;
  font-weight: 600;
  padding: 0.1rem;
  width: 3.5rem;
  height: 2rem;
  border-radius: 0.7rem;
  transition: transform 0.2s ease-in;
}

.buttons-sec button:hover {
  transform: scale(1.05);
}

.main-sec {
  grid-row: 2 / span 1;
  grid-column: 2 / span 1;
  justify-self: center;
  border-radius: 1.5rem;
  padding: 1rem;
  width: 95%;
  font-size: 1rem;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  gap: 0.5rem;
  background: linear-gradient(
    to right,
    rgba(55, 55, 109, 0.5),
    rgba(52, 109, 52, 0.5),
    rgba(133, 100, 58, 0.5)
  );
  box-shadow: 5px 5px 10px rgba(255, 255, 255, 0.5);
  z-index: 10;
}

.last-sec {
  align-self: flex-start;
  display: grid;
  justify-items: flex-end;
  align-items: flex-start;
  color: white;
  padding: 1rem 0.5rem;
  width: 95%;
  height: 35rem;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: cover;
  background-position: center center;
  cursor: context-menu;
  border-radius: 1rem;
  grid-row: 2 / -1;
  grid-column: 3 / -1;
  z-index: 2;
  transition: transform 0.3s ease-in-out;
  position: relative;
}

.last-sec > * {
  z-index: 1;
}

.last-sec::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.45);
  border-radius: 1rem;
  z-index: 0;
}

.last-sec:hover {
  transform: scale(1.05);
}

.last-sec fieldset {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 1rem;
  gap: 1rem;
  margin-top: 5rem;
}

.last-sec legend {
  font-weight: 800;
  color: black;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0.5rem;
  padding: 0.1rem 0.3rem;
  width: min-content;
}

.last-sec fieldset > div {
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 1rem;
  padding: 0.2rem 0.5rem;
  color: rgb(0, 0, 0);
  align-self: flex-end;
  display: flex;
  justify-content: flex-end;
}

.last-sec svg {
  fill: black;
}

.last-sec fieldset div div:last-child {
  width: 3rem;
  height: 3rem;
}

.last-sec fieldset div div:first-child {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.last-sec span {
  font-weight: 600;
  text-align: right;
}

.last-sec span:last-child {
  font-size: 1.2rem;
}

.small {
  font-size: 0.8rem;
}

input {
  width: fit-content;
  padding-left: 0.5rem;
}

input,
.search-sec > button {
  height: 2rem;
  font-size: 1rem;
  border-radius: 1rem;
}

.search-sec > div:first-child > button {
  width: fit-content;
  font-weight: 500;
  padding: 0 0.5rem;
}

input:hover {
  background-color: rgb(197, 223, 223);
}

h1 {
  align-self: center;
  font-size: 2rem;
  color: rgb(31, 31, 31);
  font-weight: 700;
  cursor: context-menu;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 1rem;
  padding: 0 0.5rem;
}

h2 {
  display: none;
  font-size: 2rem;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 1rem;
  padding: 0.1rem 0.5rem;
  cursor: context-menu;
}

h3 {
  justify-self: center;
  color: black;
  background-color: rgba(255, 255, 255, 0.7);
  padding: 0 0.5rem;
  border-radius: 0.5rem;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: context-menu;
}

.main-sec fieldset {
  border: 1px solid rgb(255, 255, 255);
  border-radius: 1.5rem;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1.5rem;
  cursor: context-menu;
  transition: transform 0.2s ease-in-out;
}

.main-sec fieldset:hover {
  transform: scale(1.02);
  background-color: rgba(0, 0, 0, 0.5);
}

.main-sec legend {
  font-size: 1.2rem;
  font-weight: 600;
  margin-left: 2rem;
  padding: 0.1rem 0.4rem;
  border-radius: 1rem;
  border: 1px solid white;
  background-color: rgb(0, 0, 0);
}

.location-details,
.weather-forecast {
  width: 37rem;
}

.temperature,
.wind-speed {
  display: flex;
}

.temperature button,
.wind-speed button {
  margin-left: auto;
}

.location-details div {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.weather-div {
  display: grid;
  grid-template: repeat(3, 1fr) / repeat(auto-fit, minmax(14rem, 16rem));
  justify-content: center;
  column-gap: 0.5rem;
}

.weather-div > div {
  display: grid;
  grid-template: 1fr 2fr / 1fr 3.5fr;
  column-gap: 0.3rem;
}

.weather-div > div > div {
  grid-row: 1 / -1;
  width: 2rem;
  align-self: center;
  justify-self: flex-end;
}

.weather-div > div > span {
  font-size: 0.8rem;
}

.weather-div > div > span:last-child {
  font-size: 1.2rem;
  font-weight: 600;
}

em {
  font-size: 0.9rem;
}

svg {
  fill: white;
}

.location-details fieldset span {
  font-size: 1rem;
  font-weight: 600;
}

.main-sec fieldset p {
  background-color: rgb(0, 0, 0);
  border-radius: 1rem;
  padding: 0.2rem;
}

.load {
  display: none;
  width: 1.5rem;
  height: 1.5rem;
  background: linear-gradient(to bottom, rgb(0, 31, 31), rgb(6, 141, 141));
  box-shadow: 1px 1px 5px 2px rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  animation: bounce 0.8s infinite;
}

@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

@media (max-width: 1400px) {
  body {
    gap: 1rem;
    padding: 4rem 1rem;
  }

  .location-details,
  .weather-forecast {
    width: 100%;
  }
}

@media (max-width: 800px) {
  body {
    grid-template: 0.5fr 10fr 3fr 7fr / repeat(2, 1fr);
    height: 100%;
    padding: 2rem 3rem;
    gap: 1rem;
    background: rgba(2, 116, 116, 0.5);
  }

  .search-sec {
    grid-row: 1 / 2;
    grid-column: 1 / -1;
  }

  .main-sec {
    grid-row: 2 / 3;
    grid-column: 1 / -1;
  }

  .first-sec {
    grid-row: 3 / 4;
    grid-column: 1 / 2;
  }

  .buttons-sec {
    grid-row: 3 / 4;
    grid-column: 2 / -1;
  }

  .last-sec {
    grid-row: 4 / 5;
    grid-column: 1 / -1;
  }
}

@media (max-width: 500px) {
  body {
    padding: 1rem 1rem 2rem 1rem;
    gap: 1rem;
  }
}
