, 88, 88);\n  cursor: pointer;\n}\n\n.buttons-sec button:first-child {\n  width: 100%;\n  font-weight: 500;\n}\n\n.buttons-sec button {\n  font-size: 1rem;\n  font-weight: 600;\n  padding: 0.1rem;\n  width: 3.5rem;\n  height: 2rem;\n  border-radius: 0.7rem;\n  transition: transform 0.2s ease-in;\n}\n\n.buttons-sec button:hover {\n  transform: scale(1.05);\n}\n\n.main-sec {\n  grid-row: 2 / span 1;\n  grid-column: 2 / span 1;\n  justify-self: center;\n  border-radius: 1.5rem;\n  padding: 1rem;\n  width: 95%;\n  font-size: 1rem;\n  color: white;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: space-evenly;\n  gap: 0.5rem;\n  background: linear-gradient(\n    to right,\n    rgba(55, 55, 109, 0.5),\n    rgba(52, 109, 52, 0.5),\n    rgba(133, 100, 58, 0.5)\n  );\n  box-shadow: 5px 5px 10px rgba(255, 255, 255, 0.5);\n  z-index: 10;\n}\n\n.last-sec {\n  align-self: flex-start;\n  display: grid;\n  justify-items: flex-end;\n  align-items: flex-start;\n  color: white;\n  padding: 1rem 0.5rem;\n  width: 95%;\n  height: 35rem;\n  background-image: url(./assets/rain.jpg);\n  background-size: cover;\n  background-position: center center;\n  cursor: context-menu;\n  border-radius: 1rem;\n  grid-row: 2 / -1;\n  grid-column: 3 / -1;\n  z-index: 2;\n  transition: transform 0.3s ease-in-out;\n  position: relative;\n}\n\n.last-sec > * {\n  z-index: 1;\n}\n\n.last-sec::after {\n  content: \"\";\n  position: absolute;\n  width: 100%;\n  height: 100%;\n  background-color: rgba(0, 0, 0, 0.45);\n  border-radius: 1rem;\n  z-index: 0;\n}\n\n.last-sec:hover {\n  transform: scale(1.05);\n}\n\n.last-sec fieldset {\n  display: flex;\n  flex-direction: column;\n  width: 100%;\n  padding: 1rem;\n  gap: 1rem;\n  margin-top: 5rem;\n}\n\n.last-sec legend {\n  font-weight: 800;\n  color: black;\n  background-color: rgba(255, 255, 255, 0.5);\n  border-radius: 0.5rem;\n  padding: 0.1rem 0.3rem;\n  width: min-content;\n}\n\n.last-sec fieldset > div {\n  background