video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

body {
  font-family: Arial, sans-serif;
  font-size: 16px;
  line-height: 1.1;
}

a {
  text-decoration: none;
  background-color: transparent;
}

ol,
ul {
  list-style: none;
}

main,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
