  display: block;
}

blockquote,
q {
  quotes: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  border-style: none;
}

button,
input {
  overflow: visible;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
}

strong,
b {
  font-weight: bolder;
}
